<template>
  <!-- 文章分类组件 -->
  <div class="blog-category-container" v-loading="isLoading">
    <h2>文章分类</h2>
    <RightList :list="list" @select="handleSelect" />
  </div>
</template>

<script>
import RightList from "./RightList"; //列表组件
import fetchData from "@/mixins/fetchData.js"; //混入
import { getBlogCategories } from "@/api/blog.js"; //请求组件
export default {
  mixins: [fetchData([])], //混入
  components: {
    // 注册组件
    RightList,
  },
  computed: {
    categoryId() {
      //设置参数，获取地址栏参数如果地址栏没有参数使用默认参数
      return +this.$route.params.categoryId || -1;
    },
    limit() {
      return +this.$route.query.limit || 10;
    },
    list() {
      //汇总文章数
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );
      // 给这个数组从第一位添加一个值
      const result = [
        { name: "全部", id: -1, articleCount: totalArticleCount },
        ...this.data,
      ];
      // 给这个数组的每一位这个对象添加一些需要的值
      return result.map((it) => ({
        ...it, //展开每一位
        isSelect: it.id === this.categoryId, //查看这个id是否等于路由中的id
        aside: `${it.articleCount}篇`, //加入这个下面有多少篇
      }));
    },
  },
  methods: {
    async fetchData() {
      //远程获取数据的方法
      return await getBlogCategories();
    },
    handleSelect(item) {
      const query = {
        page: 1,
        limit: this.limit,
      };
      // 跳转到 当前的分类id  当前的页容量  newPage的页码
      if (item.id === -1) {
        this.$router.push({
          // 命名路由跳转
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          // 命名路由跳转
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
          },
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.blog-category-container {
  width: 300px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  height: 100%;
  overflow-y: auto;
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>
