<template>
  <!-- 这是一个列表组件 -->

  <ul class="right-list-container">
    <li v-for="(item, i) in list" :key="i">
      <span @click="handleClick(item)" :class="{ active: item.isSelect }">
        {{ item.name }}
      </span>
      <span
        v-if="item.aside"
        @click="handleClick(item)"
        class="aside"
        :class="{ active: item.isSelect }"
      >
        {{ item.aside }}
      </span>
      <!-- 显示当前组件 -->
      <RightList :list="item.children" @select="handleClick" />
    </li>
  </ul>
</template>

<script>
export default {
  //在自己的组价中调用自己这就是组件递归
  name: "RightList", //因为要在这个组件中调用自己所以需要写一个名字
  props: {
    // 传进来的参数应该是这样的
    // [{name:"xxx",isSelect:是否选中,children:[{name:"xxx",isSelect:是否选中,children:[{}]//是否有子标签}]//是否有子标签}]
    list: {
      type: Array,
      default: () => [], //默认值为一个空数组
    },
  },
  methods: {
    handleClick(item) {
      //设置一个点击事件把这个事件传给父组件
      if (!item.isSelect) {
        this.$emit("select", item);
      }
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.right-list-container {
  list-style: none;
  padding: 0;
  .right-list-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    font-size: 14px;
    cursor: pointer;
    .active {
      color: @warn;
      font-weight: bold;
    }
  }
}
.aside {
  font-size: 12px;
  margin-left: 1em;
  color: @gray;
}
</style>
